.movie{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 0;
}

.movie + .movie{
    border-top: 1px #8841ff solid;
}

.movie h4{
    font-size: 18px;
    margin: 0;
}

.stars{
    display: flex;
    align-items: center;
    margin-bottom: 5px;
}

.star{
    height: 21px;
    width: 21px;
}

.rating{
    margin: 0;
    font-size: 18px;
}