.button{
    background: linear-gradient(135deg, rgba(54,158,217,1) 0%, rgba(182,60,225,1) 100%);
    color: white;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    border-radius: 5px;
    text-decoration: none;
}

.button:hover{
    outline: 2px #8841ff solid;
    cursor: pointer;
}